@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: jameel noori nastaleeq;
  overflow-x: hidden;
}
.arabic{
  font-family: _PDMS_Kalash;
}
main {
  padding: 30px;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  /* background: linear-gradient(180deg, #4B91F7 0%, #367AF6 100%); */
  background: linear-gradient(180deg, #29323c 0%, #485563 100%);
   /* background-color: #A46BF5; */
   color: white;
  
}


/* .container p{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
} */
.list-header {
  border-bottom:2px solid white;
  padding:10px 0px;
  display: grid;
  width: 700px;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 10px;
  cursor: pointer;
}
h1,h3,h4,h5{
  text-align: center;
}
.container{
  width: 700px;
  padding: 10px 0px;
}
.taaruf p{
  font-size: 18px;
  font-weight: 600;
  text-align: justify;
  direction: rtl;
}
.english{
  font-family: Poppins;
}
.taaruf h3{
  font-size: 35px;
  
}
.taaruf{
  padding: 10px;
}
.taaruf button{
  background: transparent;
  font-size: 18px;
  color: yellow;
  cursor: pointer;
  border: none;
}
.centered{
  
  min-height: 100vh;
  width: 700px;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  flex-direction: column;
}
.centered input{
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 2px solid white;
  padding: 10px 20px;
  color: white;
  /* border-radius: 5px; */
  width: 100%;
  margin-bottom: 20px;
}